.completed-order {
  background-color: #dff0d8; /* Light Green */
}

.pending-order {
  background-color: #fcf8e3; /* Light Yellow */
}

.cancelled-order {
  background-color: #f2dede; /* Light Red */
}

.dashboard-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
}

.dashboard-title {
  font-size: 2rem;
  margin-bottom: 20px;
}

.orders-table {
  width: 80%;
}
