/* Navbar.css */

/* Header layout with padding and alignment */
.header {
  background-color: #001529;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  height: 64px;
}

/* Navbar content flexbox to ensure proper alignment */
.navbar-content {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
}

/* Styling for active menu links */
.ant-menu-item-selected {
  background-color: transparent !important;
  border-bottom: 2px solid #1890ff;
}

/* Improve readability for active links */
.active-link {
  color: #1890ff !important;
  font-weight: bold;
  text-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}

/* Styling for normal links to maintain contrast */
.ant-menu-item {
  color: white !important;
}

/* Styling for basket icon */
.basket-icon {
  margin-left: 20px;
}

/* Ensure horizontal layout remains responsive */
.ant-menu-horizontal {
  border-bottom: none;
  flex-grow: 1;
}

/* Adjust hover effect for better UX */
.ant-menu-item:hover {
  background-color: rgba(255, 255, 255, 0.1);
}
